//== BL Colors
@primary-1:             #004e88;
@primary-2:             #2a7fb9;
@primary-3:             #69a5ce;
@primary-4:             #9bc9e8;


@bl-sante:              #2B957A;
@bl-territorial:		#2F87C3;
@bl-default:            #564AA3;

@bl-light:              @bl-default;
@bl-dark:               #640c0e;


@secondary-1:           #004e88;
@secondary-2:           #2a80b9;
@secondary-3:           #54b4e2;
@secondary-4:           #419641;
@secondary-5:           #f39c11;
@secondary-6:           #b86c9a;
@secondary-7:           #e84c3d;
@secondary-8:           #666666;
@secondary-9:           #a5aaae;
@secondary-10:          #eaf4fb;

@black:                 #333333;
@white:                 #ffffff;

//== Bootstrap Colors
@gray-base:              @secondary-8;
@gray-darker:            lighten(@gray-base, 13.5%);
@gray-dark:              lighten(@gray-base, 20%);
@gray:                   lighten(@gray-base, 33.5%);
@gray-light:             lighten(@gray-base, 46.7%);
//@gray-lighter:           lighten(@gray-base, 57%);
@gray-lighter:			#f5f7fa;

@brand-primary:         @secondary-2;
@brand-success:         @secondary-4;
@brand-warning:         @secondary-5;
@brand-danger:          @secondary-7;
@brand-default:			#406274;

//== Typography
//

@font-size-base:          12;
@font-size-large:         ceil((@font-size-base * 1.25));
@font-size-small:         ceil((@font-size-base * 0.85));

@font-size-h1:            floor((@font-size-base * 1.8));
@font-size-h2:            floor((@font-size-base * 1.6));
@font-size-h3:            ceil((@font-size-base * 1.4)); 
@font-size-h4:            ceil((@font-size-base * 1.15));
@font-size-h5:            @font-size-base;
@font-size-h6:            ceil((@font-size-base * 0.85));

@line-height-computed:    (floor((@font-size-base * @line-height-base)) * 1px); // ~20px
