//== Scaffolding
//@body-bg:               #f9f9f9;
@body-bg:               @gray-lighter;
@text-color:            @black;
@link-color:            @primary-2;
@link-hover-color:      @secondary-1;

//== Typography
@font-family-sans-serif:  Arial, sans-serif;
@font-family-serif:       "open_sansregular";

@font-family-base:        @font-family-sans-serif;
//@font-size-base:          12px;


//== NavBar
@navbar-height: 30px;

//== Tabs
@nav-tabs-border-color:                           @bl-light;
@nav-tabs-link-hover-border-color:                @gray-lighter;
@nav-tabs-active-link-hover-color:                white;
@nav-tabs-active-link-hover-border-color:         @bl-light;
@nav-tabs-justified-link-border-color:            @bl-light;
@nav-tabs-justified-active-link-border-color:     @body-bg;

//== Modals
@modal-content-bg:                             #ffffff;
@modal-content-border-color:                   @primary-2;
@modal-backdrop-bg:           @black;
@modal-backdrop-opacity:      .6;
@modal-footer-border-color:   @primary-2;

//== Breadcrumbs
@breadcrumb-bg:                 inherit;
@breadcrumb-color:              @primary-2;
@breadcrumb-click-color:        @primary-1;
@breadcrumb-active-color:       @black;
@breadcrumb-separator:          "/";
@breadcrumb-padding-vertical:   15px;
@breadcrumb-padding-horizontal: 11px;


//== Badges
//
//##
@badge-bg:                    @gray-darker;


//== Dropdowns
@dropdown-bg:                    #ffffff;
@dropdown-border:                @secondary-9;
@dropdown-divider-bg:            @secondary-9;
@dropdown-link-color:            @black;
@dropdown-link-hover-color:      @black;
@dropdown-link-hover-bg:         #aad1eb;

//== Forms
@input-bg:                       #ffffff;
@input-bg-disabled:              #ebebeb;
@input-color:                    @black;
@input-border:                   @secondary-9;
@input-border-radius:            3px;
@input-border-radius-large:      @input-border-radius;
@input-border-radius-small:      @input-border-radius;
@input-border-focus:             @secondary-3;
@input-color-placeholder:        #999;
@input-height-base:              25px;
@input-height-large:             35px;
@input-height-small:             @input-height-base;
@legend-color:                   @primary-1;
@legend-border-color:            @primary-1;
@cursor-disabled:                text;
@legend-color:                   @primary-1;
@legend-border-color:            @primary-1;
@form-label-color:                    #444444;
@form-label-size:                     @font-size-base;

//== List group
@list-group-bg:                 #ffffff;
@list-group-border:             #cccccc;
@list-group-border-radius:      4px;
@list-group-hover-bg:           #aad1eb;
@list-group-active-bg:          @primary-2;
@list-group-active-text-color:  #ffffff;

//== Tables
@table-bg-accent:               #f1f4f8;
@table-bg-hover:                #aad1eb;
@table-bg-active:               @primary-2;

//== Form states and alerts
@state-success-text:             @brand-success;
@state-success-bg:               lighten(@brand-success, 40%);
@state-success-border:           lighten(@brand-success, 30%);

@state-info-text:                @primary-2;
@state-info-bg:                  lighten(@primary-2, 40%);
@state-info-border:              lighten(@primary-2, 30%);

@state-warning-text:             @brand-warning;
@state-warning-bg:               lighten(@brand-warning, 30%);
@state-warning-border:           lighten(@brand-warning, 20%);

@state-danger-text:              @brand-danger;
@state-danger-bg:                lighten(@brand-danger, 30%);
@state-danger-border:            lighten(@brand-danger, 20%);

//== Labels
@label-color:                    @white;

//== Alerts
@alert-padding:               15px;
@alert-border-radius:         0px;
@alert-link-font-weight:      bold;

@alert-success-bg:            darken(@state-success-bg, 20%);
@alert-success-text:          @white;
@alert-success-border:        darken(@state-success-bg, 30%);

@alert-info-bg:               darken(@state-info-bg, 20%);
@alert-info-text:             @white;
@alert-info-border:           darken(@state-info-bg, 30%);

@alert-warning-bg:            darken(@state-warning-bg, 20%);
@alert-warning-text:          @white;
@alert-warning-border:        darken(@state-warning-bg, 30%);

@alert-danger-bg:             darken(@state-danger-bg, 20%);
@alert-danger-text:           @white;
@alert-danger-border:         darken(@state-danger-bg, 30%);

//== Menu
//@menu-bg-color:               #E6E9EE;
@menu-bg-color:               @white;
@menu-bg-color-active:        lighten(#D9DBE0, 7%);
@menu-color-active:           #555555;
@menu-border-color-active:    @bl-light;
@menu-hover-border-color:     @bl-light;
@menu-bg-hover-color:		  darken(#EFF1F4, 6%);
@menu-font-size:              @font-size-base;


@padding-base-vertical:     3px;

